import React, { PureComponent } from 'react';
import { withRouter } from 'react-router-dom';
import './index.css'
import Header from '../../Component/Header'
import Web3 from 'web3';
import { Modal } from 'react-pure-css-modal';
import Token from '../../Contract/Lottery/interface.js'


try {
  var web3 = window.web3;
  var web3 = new Web3(web3.currentProvider);
} catch (err) {
  console.log(err)
}
try {
  const Contract = web3.eth.contract(Token.ABI).at(Token.Address);
  window.Contract = Contract;
} catch (err) {
  var Contract;
  var web3 = {
    eth: { accounts: [] }
  }
}

var num = 0;
var i = 0;
var runIndex = 7;
class lobby extends PureComponent {
  constructor() {
    super();
    this.state = {
      i18n: 'en',
      depositAmount: 0.01
    }
  }
  componentWillMount() {
    function timer(intDiff) {

      var intDiff = parseInt(intDiff);
      var day = 0,
        hour = 0,
        minute = 0,
        second = 0;

      window.setInterval(function () {

        if (intDiff > 0) {
          day = Math.floor(intDiff / 86400);
          hour = Math.floor(intDiff / 3600) - (day * 24);
          minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
          second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        }
        if (hour <= 9) hour = "0" + hour;
        if (minute <= 9) minute = "0" + minute;
        if (second <= 9) second = "0" + second;
        document.getElementById('countdown_day').innerText = day;
        document.getElementById('countdown_hour').innerText = hour;
        document.getElementById('countdown_minute').innerText = minute;
        document.getElementById('countdown_second').innerText = second;
        // console.log(" " + day + ":" + hour + ":" + minute + ":" + second + "");
        intDiff--;
      }, 1000);
      // alert("The language is: " + userLang);
    }
    //  js  0    ICO
    let elapsed = Math.floor(Date.now() / 1000) - Math.floor(new Date(2018, 7 - 1, 27).getTime() / 1000);
    timer(60 * 60 * 24 - elapsed);
  }

  htmlstrFun(id) {
    var div_ul = `<div id=${id}><ul>`;
    var ul_div = '</ul></div>';
    var li_li = '';
    for (let i = 0; i < 10; i++) {
      li_li += '<li>' + i + '</li>';
    }
    return div_ul + li_li + li_li + ul_div;
  }


  componentDidMount() {
    // var c = "";
    // for (let i = 0; i <= 7; i++) {
    //   c = c + this.htmlstrFun(`c_0${i}`)
    // }
    // document.getElementById('money').insertAdjacentHTML('beforeend', c);

    // this.runPrize("123499.7993")

    // setTimeout(() =>this.runPrize("564781.2451") , 8000)
  }

  runPrize(target) {
    var target = Math.floor((target) * 100).toString();
    console.log(target)
    if (!window.prizePool) {
      // 
      window.prizePool = target;
    } else {
      // 
      for (let i = 0; i < target.length; i++) {
        if (window.prizePool[i] !== target[i]) {
          this.runNum(1, i, target[i], 'single')
        }
      }
      return
    }
    setTimeout(() => {
      for (let i = 0; i <= 7; i++) {
        this.runNum(1, i, target, 'all')
      }
    }, 1000)
  }

  confirmDeposit() {
    try {
      window.Contract.buy({
        from: web3.eth.accounts[0],
        value: parseFloat(this.state.depositAmount) * 10 ** 18,
        gas: 511700,
        gasPrice: 5100000000
      }, (e, r) => {
        console.log(e);
        if (!e) {
          alert('Success deposit');
        }
      })
    } catch (err) {
      console.log(err)
    }
  }

  runNum(fe, index, target, type) {
    let gap = document.querySelector('.bonus_money li').clientHeight;
    let position = 0;
    if (type === 'all') {
      var _target = target[index];
    } else if (type === 'single') {
      var _target = target;
    }

    let extra = 0
    if (_target === "9") extra = gap / 2
    var v = setInterval(() => {
      if (position === -(gap * 10) - (gap * _target) - extra && type === 'single') {
        clearInterval(v);
        return
      }
      if (position === Math.floor(-(gap * 10) - (gap * _target) + extra) && index == runIndex) {
        if (extra !== 0) document.querySelector(`#c_0${index}`).style.top = `${position - extra}px`;
        runIndex -= 1;
        clearInterval(v);
        if (runIndex === -1) {
          // reset
          runIndex = 7;
        }
        return
      }
      if (position === -(gap * 19) + Math.floor(gap / 2)) {
        position = -(gap * 9) + Math.floor(gap / 2)
      } else {
        position -= 1;
      }
      document.querySelector(`#c_0${index}`).style.top = `${position}px`;
    }, fe)
  }

  render() {
    const l = window.context.state.langObject
    return (
      <div className="lobby_bg">
        <Header />
        <div className="ui-counter">
          <div className="days" style={{ textAlign: 'center' }}>
            <h3 id="countdown_day" className="value"></h3>
            <p className="label">{l['days']}</p>
          </div>
          <div className="hours" style={{ textAlign: 'center' }}>
            <h3 id="countdown_hour" className="value"></h3>
            <p className="label">{l['hours']}</p>
          </div>
          <div className="minutes" style={{ textAlign: 'center' }}>
            <h3 id="countdown_minute" className="value"></h3>
            <p className="label">{l['minutes']}</p>
          </div>
          <div className="seconds" style={{ textAlign: 'center' }}>
            <h3 id="countdown_second" className="value"></h3>
            <p className="label">{l['seconds']}</p>
          </div>
        </div>
        <div style={{ marginLeft: '25px', fontWeight: 'bold' }}> {l['ico_end']} July 27, 2018. </div>

        <div onClick={() => document.getElementById('depositModal').click()} style={{ position: 'fixed', top: '0px', right: '0px' }}>
          <a style={{background: 'rgb(135, 233, 210)'}} className="waves-effect waves-light btn-large"><div>{l['deposit']}</div></a>
        </div>
        {/* <div style={{ display: 'flex' }} className="bonus_money" id="money"></div> */}
        <div className="loginBlock">

        </div>
        <div onClick={() => this.props.history.push("/worldcup")} className="toFIFAbtn">{l['jumpworldcup']}</div>


        <Modal id="depositModal" onClose={() => { console.log("Modal close") }} >
          <div style={{ textAlign: 'center', fontSize: '30px',marginTop: '10px' }}>{l['deposit']}</div>
          <div style={{ textAlign: 'center', marginTop: '50px' }}>
            <span>Amount</span><input type="number" onChange={(e) => this.setState({ depositAmount: e.target.value })} defaultValue={this.state.depositAmount} style={{ width: '15%', marginLeft: '10px', textAlign: 'center' }} /> ETH
            <div style={{ marginTop: '30px' }}>
              <button onClick={() => this.confirmDeposit()} className="btn waves-effect waves-light" type="submit" name="action">Confirm</button>
            </div>
          </div>
        </Modal>
      </div>

    );
  }
}

export default withRouter(lobby);


