import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

class Header extends PureComponent {
  changeLang(e) {
    localStorage.setItem('lang', e.target.value)
    window.context.setState({'lang': e.target.value});
    window.context.changeLang();
    // window.context.forceUpdate()
  }
  render() {
    return (
      <div>
        <select className="langSelect" style={{display: 'block', width: '120px'}} defaultValue={localStorage.getItem('lang') ? localStorage.getItem('lang') : window.navigator.userLanguage || window.navigator.language} onChange={(e) => this.changeLang(e)}>
          <option value="en">English</option>
          <option value="zh-TW">-</option>
          <option value="zh-CN">-</option>
        </select>
      </div>
    );
  }
}

Header.propTypes = {

};

export default Header;


