import React, { PureComponent } from 'react';
import { withRouter } from 'react-router-dom';
import { Modal } from 'react-pure-css-modal';
import Header from '../../Component/Header'
import './index.css';
import teamData from '../../__data__/team.js'
import Token from '../../Contract/Lottery/interface.js'
import Web3 from 'web3';
var web3 = window.web3;

var Contract;
if (typeof web3 !== 'undefined') {
  var web3 = new Web3(web3.currentProvider);
  Contract = web3.eth.contract(Token.ABI).at(Token.Address.ropsten);
  window.Contract = Contract;
} else {
  var web3 = { eth: { accounts: [] } }
  // alert('Please install Metamask plugin first.')
  // window.location.href = 'https://metamask.io/';
}

console.log(Token.Address)


// console.log(toTimestamp('06/22/2018 20:00:00'));

class WorldCup extends PureComponent {
  constructor() {
    super();
    this.state = {
      user: '',
      mainSelect: '',
      loading: false,
      depositAmount: 0.1,
      network: '',
      countryPic: teamData['countryPic'],
      teams: teamData['teams'],
      currentTeam: {
        time: '',
        match: ['', ''],
        odds: [1.2, 7, 15]
      }
    }
  }
  componentDidMount() {
    this.getUserToken();
    this.setState({ user: web3.eth.accounts[0] }, () => {
      // Metamask
      var accountInterval = setInterval(() => {
        if (web3.eth.accounts[0] !== this.state.user && typeof web3.eth.accounts[0] !== 'undefined') {
          window.location.reload();
        }
        if (typeof web3.eth.accounts[0] === 'undefined') {
          this.setState({ user: 'Please login into MetaMask.' })
        }
      }, 1000);
      try {
        web3.version.getNetwork((err, netId) => {
          switch (netId) {
            case "1":
              console.log('This is mainnet');
              this.setState({ network: 'mainnet' })
              Contract = web3.eth.contract(Token.ABI).at(Token.Address.main);
              window.Contract = Contract;
              this.getUserToken();
              break
            case "3":
              console.log('This is the ropsten test network.')
              this.setState({ network: 'ropsten' })
              Contract = web3.eth.contract(Token.ABI).at(Token.Address.ropsten);
              window.Contract = Contract;
              this.getUserToken();
              break
            default:
              console.log('This is an unknown network.')
          }
        })
      } catch (err) {

      }
    });
  }

  toTimestamp(strDate) {
    var datum = Date.parse(strDate);
    return (datum)
  }

  getUserToken() {
    try {
      Contract.balanceOf(web3.eth.accounts[0], (e, r) => {
        this.setState({ userToken: web3.toDecimal(r) / 10 ** 18 })
      })
    } catch (err) {
    }
  }

  confirmDeposit() {
    console.log(this.state.depositAmount)
    try {
      Contract.buy({
        from: web3.eth.accounts[0],
        value: parseFloat(this.state.depositAmount) * 10 ** 18,
        gas: 511700,
        gasPrice: 5100000000
      }, (e, r) => {
        console.log(e);
        if (!e) {
          alert('Success deposit');
        }
      })
    } catch (err) {

    }
  }

  betConfirm() {
    if (!this.state.userbetTeam || this.state.userbetTeam === 'Select a Team') {
      alert('Team not selected.');
      return
    }
    if (!this.state.userbetAmount) {
      alert('Amount not set');
      return
    }
    if (this.state.userToken < this.state.userbetAmount) {
      alert('Balance not enough');
      return
    }
    var betInfo = {
      userbetTeam: this.state.userbetTeam,
      userbetAmount: this.state.userbetAmount
    }
    try {
      Contract.transfer("0x76d87e6A4069E4a463Ec6a1EB7B73738282906D5", this.state.userbetAmount * 10 ** 18,
        JSON.stringify(betInfo)
        , {
          from: web3.eth.accounts[0],
          gas: 511700,
          gasPrice: 5100000000
        }, (e, r) => {
          console.log(e);
          if (!e) {
            alert('Success Bet');
          }
        })
    } catch (err) {

    }
  }

  render() {
    const l = window.context.state.langObject
    return (
      <div>
        {
          this.state.loading
            ?
            <div style={{ margin: '0px', position: 'absolute', zIndex: 10001 }} className="progress">
              <div className="indeterminate"></div>
            </div>
            : ''
        }
        <div style={{ position: 'fixed', zIndex: '10000', height: '150px', width: '100%', background: '#0b5f91', paddingTop: '20px', paddingLeft: '20px', boxShadow: "10px 10px grey" }}>
        
        <Header/>
          <div style={{ marginTop: '50px', color: 'white', display: 'inline', fontSize: '30px' }}>FIFA World Cup</div>
          <div className="mainSelect" style={{ display: 'flex', marginTop: '25px' }}>
            <div onClick={() => this.setState({ mainSelect: 'MATCHES', loading: false })} className={this.state.mainSelect === "MATCHES" ? "select_underscore" : 'select_underscore'}>{l['matches']}</div>
            {<div onClick={() => {
              this.setState({
                mainSelect: 'How To Play', loading: false
              });
              document.getElementById('HOWTO_modal').click();
            }} className={this.state.mainSelect === "How To Play" ? "" : ''}>{l['howtoplay']}</div>}
          </div>

          <div>
            <button onClick={() => {
              document.getElementById('depositModal').click();
              this.getUserToken();
            }} className="btn waves-effect waves-light" type="submit" name="action">{l['deposit']}</button>
          </div>

          <div style={{ position: 'fixed', top: '0px', right: '0px' }}>
            <button onClick={() => document.getElementById('personalInfoModal').click()} className="btn waves-effect waves-light" type="submit" name="action">{this.state.user}</button>
          </div>
          <div className="discordIcon" onClick={() => window.open('https://discordapp.com/invite/tkScqV')} style={{ position: 'fixed', top: '40px', right: '0px' }}><img style={{ width: '60px', height: '60px' }} src="https://discordapp.com/assets/f8389ca1a741a115313bede9ac02e2c0.svg" /></div>
        </div>

        <div style={{ textAlign: 'center', position: 'relative', top: '200px' }}>
          <div style={{ background: 'rgb(245,245,245)', height: '50px', width: '80%', display: 'inline-block', marginTop: '15px', lineHeight: '50px' }}>MATCHS</div>
          <div className="cardGroup" style={{ display: 'flex', flexWrap: 'wrap', width: '100%', justifyContent: 'center' }}>
            {this.state.teams.map(team => (
              Date.now() < parseInt(this.toTimestamp(team.time)) ?
                <div style={{ background: 'white', width: '40%', height: '200px' }}>
                  <div>{team.time}</div>
                  <div style={{ display: 'flex', justifyContent: 'center', marginTop: '20px' }}>
                    <div>
                      <img style={{ width: '35px', height: '35px' }} src={this.state.countryPic[team.match[0]]} alt="" />
                      <div>{team.match[0]}</div>
                    </div>
                    <div style={{ width: "70px", lineHeight: "50px" }}>V.S</div>
                    <div>
                      <img style={{ width: '35px', height: '35px' }} src={this.state.countryPic[team.match[1]]} alt="" />
                      <div>{team.match[1]}</div>
                    </div>
                  </div>
                  {Date.now() < parseInt(this.toTimestamp(team.time)) ?
                    <button onClick={() => {
                      document.getElementById('betModal').click();
                      this.setState({ currentTeam: team })
                    }} style={{ margin: '10px' }} className="btn waves-effect waves-light" type="submit" name="action">{l['bet']}</button>
                    : ''}
                  <br />
                  <a target="_blank" href="https://www.fifa.com/worldcup/">{l['matchpreview']}</a>
                </div>
                : ''
            ))}

          </div>
        </div>


        <Modal id="depositModal" onClose={() => { console.log("Modal close") }} >
          <div style={{ textAlign: 'center', fontSize: '30px' }}>{l['deposit']}</div>
          <div style={{ textAlign: 'center', marginTop: '50px' }}>
            <span>Amount</span><input type="number" onChange={(e) => this.setState({ depositAmount: e.target.value })} defaultValue={this.state.depositAmount} style={{ width: '10%', marginLeft: '10px', textAlign: 'center' }} /> ETH
            <div style={{ marginTop: '30px' }}>
              <button onClick={() => this.confirmDeposit()} className="btn waves-effect waves-light" type="submit" name="action">Confirm</button>
            </div>
          </div>
        </Modal>

        <Modal id="personalInfoModal" onClose={() => { console.log("Modal close") }} >
          <div style={{ textAlign: 'center', fontSize: '30px' }}>{l['personalInfo']}</div>
          <div style={{ textAlign: 'center', marginTop: '50px' }}>
            <div>Account</div><span>{this.state.user}</span>
            <div style={{ marginTop: '10px' }}>Token Hold</div>
            <div>{this.state.userToken}</div>
          </div>
        </Modal>

        <Modal id="betModal" onClose={() => { console.log("Modal close") }} >
          <div style={{ textAlign: 'center', fontSize: '30px' }}>{l['betInfo']}</div>
          <div style={{ textAlign: 'center', marginTop: '50px' }}>
            <div>Team</div>
            <select onChange={(e) => this.setState({ userbetTeam: e.target.value })} style={{ display: 'block' }}>
              <option >Select a Team</option>
              <option>{this.state.currentTeam.match[0]} {this.state.currentTeam.odds[0]}</option>
              <option>{this.state.currentTeam.match[1]} {this.state.currentTeam.odds[2]}</option>
              <option>Push {this.state.currentTeam.odds[1]}</option>
            </select>
            <div>Amount</div>
            <input onChange={(e) => this.setState({ userbetAmount: e.target.value })} defaultValue={this.state.userbetAmount} type="number" style={{ width: '100px', marginLeft: '30px', textAlign: 'center' }} /> Token
            <br />
            <button onClick={() => this.betConfirm()} className="btn waves-effect waves-light">{l['confirm']}</button>
          </div>
        </Modal>

        <Modal style={{ width: '60%', left: '20%' }} id="HOWTO_modal" onClose={() => { console.log("Modal close") }} >
          <div style={{ textAlign: 'center', fontSize: '30px' }}>{l['howtoplay']}</div>
          <div style={{ textAlign: 'center', marginTop: '50px' }}>
            <div>1. Click the deposit button then send the ETH to the contract, it will then transfer Token to your account with rate 1 : 2</div><br />
            <div>2. You can check the Token balance by click the right top address button.</div><br />
            <div>3. Click the Bet button under the match teams and you Token to bet.</div><br />
            <div>4. If you win the Bet the token will be transfer to your account next day.</div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default withRouter(WorldCup);


