import React, { PureComponent, createContext } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
} from 'react-router-dom'
import './App.css'


import tw from './i18n/zh-tw.js'
import cn from './i18n/zh-cn.js'
import en from './i18n/en.js'


// import GuessNum from './Container/GuessNum/';
// import Roll from './Container/Roll/'
import Lobby from './Container/Lobby/'
import WorldCup from './Container/WorldCup/'

// Initialize a context
const Context = createContext()

// This context contains two interesting components
const { Provider, Consumer } = Context
// i18n
if (!localStorage.getItem('lang')) {
  const userLang = window.navigator.language || window.navigator.userLanguage;
  localStorage.setItem('lang', userLang)
}
window.lang = (data) => {
  if (localStorage.getItem('lang') === "zh-TW") {
    return tw[data]
  }
  if (localStorage.getItem('lang') === "en") {
    return en[data]
  }
  if (localStorage.getItem('lang') === "zh-CN") {
    return cn[data]
  }
}

class App extends PureComponent {
  constructor() {
    super();
    this.state = {
      lang: '',
      langObject: ''
    }
  }
  componentWillMount() {
    this.changeLang();
    // if(window.location.pathname === '/') {
    //   window.location.href = '/worldcup';
    // }
  }
  changeLang() {
    let lang = localStorage.getItem('lang');
    this.setState({ lang }, () => {
      if (lang === "zh-TW") {
        this.setState({ langObject: tw })
      }
      if (lang === "en") {
        this.setState({ langObject: en })
      }
      if (lang === "zh-CN") {
        this.setState({ langObject: cn })
      }
    });
  }


  // if(window.location.pathname !== '/login' 
  //   && typeof getCookie('Authorization') === "undefined") {
  //   window.location = '/login';
  // }

  render() {
    window.context = this;
    return (
      <Router>
        <div>
          {/* <Route path="/guessnum" component={GuessNum} />
          <Route path="/roll" component={Roll} />
           */}
          {/* <Route exact path="/" component={Lobby} /> */}
          <Route path="/" component={WorldCup} />
          {/* <Route path="/register" component={Register} /> */}
        </div>
      </Router>
    );
  }
}

export default App;



